.select {
  display: flex; }
  .select .select__dropdown {
    width: 100%; }
  .select .ant-select-selection__rendered {
    display: flex;
    margin: 0;
    line-height: 36px;
    width: 100%;
    position: absolute;
    z-index: 1; }
    .select .ant-select-selection__rendered ul > li {
      font-size: 12px; }
  .select .ant-select-selection__placeholder {
    left: 11px;
    font-size: 14px;
    color: #aba9a8; }
  .select .ant-select-selection-selected-value {
    margin-left: 11px; }
  .select .ant-select-search__field__wrap {
    margin-left: 11px; }
  .select .ant-select-search__placeholder {
    left: 11px;
    font-size: 12px;
    color: #aba9a8; }
  .select__dropdown {
    display: inline-flex;
    background: #fff;
    margin-bottom: 0;
    cursor: pointer; }
  .select__input {
    min-width: 200px; }
  .select__icon {
    transform: translate(-30px);
    width: 0px;
    height: 36px;
    display: flex;
    align-items: center; }
    .select__icon .material-icons {
      -webkit-transition: -webkit-transform 0.3s;
      transition: -webkit-transform 0.3s;
      -o-transition: transform 0.3s;
      transition: transform 0.3s;
      transition: transform 0.3s, -webkit-transform 0.3s; }
      .select__icon .material-icons.open {
        transform: rotate(-180deg); }
